import * as React from "react";
import * as PropTypes from "prop-types";

const isComponent = /^[A-Z][a-zA-Z0-9]*(__[a-z][a-zA-Z0-9]+)*(--[a-z][a-zA-Z0-9]+)*$/;

export interface NamespaceWrapperProps {
  namespace ?: string;
}

export default abstract class NamespaceWrapper<P = {}, S = {}> extends React.Component<P & NamespaceWrapperProps, S> {
  static contextTypes = {
    namespace: PropTypes.string
  };

  getNamespace(): string  {
    return this.props.namespace || this.context.namespace;
  }

  classes(...args): string {
    const raw = args.filter(item => item);
    const prefix = this.getNamespace();
    const namespaced = prefix ? raw
      .filter(item => item.match(isComponent))
      .map(item => `${prefix}-${item}`) : [];
    return raw.concat(namespaced).join(" ");
  }
};



