import * as React from "react";
import * as ReactDOM from "react-dom";
import { Manager, Popper, Target, IPopperProps } from "react-popper";
import Portal from "react-travel";

import NamespaceWrapper, { NamespaceWrapperProps } from "../utils/NamespaceWrapper";
import { isIE9OrBelow, isRTL } from "../utils";

export interface DropdownProps extends NamespaceWrapperProps {
  disabled?: boolean;
  up?: boolean;
  open?: boolean;
  block?: boolean;
  size?: "small" | "medium" | "large";
  className?: string;
  trigger: ((isOpen: boolean, caret: React.ReactNode) => React.ReactNode) | React.ReactNode;

  style?: React.CSSProperties;
}

export interface DropdownState {
  open?: boolean;
}

export default class Dropdown extends NamespaceWrapper<DropdownProps, DropdownState> {

  constructor(props) {
    super(props);

    this.state = {
      open: !!props.open || false
    };
  }

  handleClick = (event) => {
    if (this.props.open) {
      return;
    }

    this.setState({
      open: !this.state.open
    });
    event.preventDefault();
  }

  renderTrigger() {
    if (typeof this.props.trigger === "function") {
      return this.props.trigger(this.state.open, <span
        className={this.classes("Caret")}></span>);
    }

    return this.props.trigger;
  }

  getPlacement(up: boolean): "top-end" | "bottom-end" | "top-start" | "bottom-start" {
    if (this.refs["manager"] && isRTL(ReactDOM.findDOMNode(this.refs["manager"]))) {
      return up ? "top-end" : "bottom-end";
    } else {
      return up ? "top-start" : "bottom-start";
    }
  }

  render() {
    const {namespace, disabled, open, size, up, trigger, children, className, block, ...props} = this.props;

    const classes = this.classes(
      "Dropdown",
      (disabled && "is-disabled"),
      (this.state.open && "is-open"),
      (size && "Dropdown--" + size),
      (up && "Dropdown--dropup"),
      (block && "Dropdown--block"),
      (isIE9OrBelow() && "Dropdown--legacy"),
      className
    );

    let ManagerComponent = isIE9OrBelow() ? "div" : Manager;
    let TargetComponent = isIE9OrBelow() ? "button" : Target;

    // TODO :: report "block" and "size" to the child popper automatically

    return <ManagerComponent className={classes} tabIndex={0} ref="manager" {...props}>
      <TargetComponent className={this.classes("Dropdown__trigger")}
                       onClick={this.handleClick}>{this.renderTrigger()}</TargetComponent>

      {this.renderPopper(children, up)}
    </ManagerComponent>;
  }

  renderPopper(children, up) {
    if (!this.state.open) {
      return;
    }

    if (isIE9OrBelow()) {
      return <div className={this.classes("Dropdown__popper")}>
        {children}
      </div>;
    }

    return <Portal>
      <Popper placement={this.getPlacement(up)}>
        {children}
      </Popper>
    </Portal>;
  }
}



